<template>
  <Dialog :isShow="isShow" @confirm="confirm" @cancel="cancel">
    <div slot="form">
      <el-form label-position="right" label-width="80px" :model="row">
        <el-form-item label="名字">
          <el-input v-model="row.username"></el-input>
        </el-form-item>
        <el-form-item label="权限">
          <el-checkbox-group
            v-model="checked"
            @change="handleCheckedCitiesChange"
          >
            <el-checkbox
              v-for="(item, index) in row.role"
              :label="item.id"
              :key="index"
              name="role"
              >{{ item.name }}</el-checkbox
            >
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
  </Dialog>
</template>

<script>
import Dialog from "components/common/dialog/Dialog";
export default {
  components: {
    Dialog,
  },
  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
    row: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      checked: [],
    };
  },
  methods: {
    confirm() {
      this.$emit("confirm", {
        id: this.row.id,
        role: this.checked,
        edit: this.row.edit,
      });
    },
    cancel() {
      this.$emit("cancel");
    },
    handleCheckAllChange(val) {
      console.log(val);
    },
    handleCheckedCitiesChange(value) {
      console.log(value);
    },
  },
};
</script>

